import { StorageFile } from '../types';

export function fileToStorageFile(
  file: File,
  bucket: string,
  folder: string
): StorageFile {
  return {
    type: 'file',
    name: file.name,
    size: file.size,
    contentType: file.type,
    bucket,
    fullPath: folder + '/' + file.name,
    uploading: true,
    updated: new Date().toUTCString(),
    timeCreated: new Date().toUTCString(),
    customMetadata: {},
  } as StorageFile;
}
